/* Navbar */
.navbar-nav a {
  font-size: 18px;
}

.background {
  margin-right: 3px;
}

.box {
  padding: 23px;
  width: 30%;
  position: absolute;
  z-index: 3;
  top: 40px;
  color: #fff;
  right: 6px;
}

.bttns {
  margin-left: -45px;

}

.input-box {
  width: 312px;
  background-color: #86d0d9;

  border-radius: 28px;
  padding: 0 20px;
}

.searchFont {
  color: black;
  text-decoration: none;
}

.Cart {
  color: red;
}

.btns {
  color: darkgray;
  margin-top: 6px;
}

.productDetImg {
  height: 400px;
  width: 400px;
}


@media (max-width : 1200px) {
  .box {
    right: 56px;
  }

}

@media (max-width : 925px) {
  .box {
    right: 0px;
    width: 45%;
  }

}

@media (max-width : 650px) {
  .box {
    right: 0px;
    width: 100%;
    top: 75px;
  }

}

@media (max-width : 390px) {
  .input-box {
    width: 275px;
  }

  .productDetImg {
    height: 250px;
    width: 250px;
  }

}

@media (max-width : 340px) {
  .input-box {
    width: 210px;
  }

}